.postItem {
  position: relative;
  padding-top: 30px;
  border-bottom: 0.5px solid rgba(22, 24, 35, 0.12);
  &:first-child {
    padding-top: 0;
  }
  .userAvatar {
    float: left;
    position: relative;
    cursor: pointer;
    .avatarImg {
      width: 56px;
      height: 56px;
      border-radius: 50%;
      object-fit: cover;
    }
  }
  .postContent {
    position: relative;
    margin-left: 68px;
    margin-bottom: 20px;
    .infoContent {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      display: flex;
      flex-flow: wrap;
      .authorUnique,
      .fullName {
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 0px;
      }
      .authorUnique {
        margin-right: 4px;
        font-weight: 600;
      }
      .fullName {
        font-weight: 400;
      }
    }
    .caption {
      strong {
        color: #121212;
        font-size: 1.6rem;
        font-weight: normal;
        display: block;
        margin-bottom: 12px;
      }
    }
    .btnFollow {
      position: absolute;
      right: 0;
      top: 8px;
    }
    .video {
      cursor: pointer;
    }
    .videoItem {
      position: relative;
      display: inline-block;
      .cardWrapper {
        .cardImage {
          position: relative;
          display: flex;
          width: 100%;
          height: 100%;
          overflow: hidden;
          border-radius: 8px;
          -webkit-box-pack: center;
          justify-content: center;
          &:hover .playBtn {
            opacity: 1;
            visibility: visible;
          }
          .overlay {
            position: absolute;
            left: 0px;
            right: 0px;
            bottom: 0px;
            height: 200px;
            pointer-events: none;
            background: linear-gradient(rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
          }
          .vlBtn {
            .vlIcon {
              cursor: pointer;
              position: absolute;
              bottom: 45px;
              right: 30px;
              font-size: 3.5rem;
              color: #fff;
            }
          }
          .playBtn {
            cursor: pointer;
            position: absolute;
            left: 30px;
            bottom: 48px;
            font-size: 2rem;
            background-color: transparent;
            color: #fff;
            visibility: hidden;
            opacity: 0;
            transition: 0.3s ease;
          }
        }
      }
    }
  }
  .iconAction {
    display: inline-block;
    margin-left: 30px;
    .itemWrapper {
      text-align: center;
    }
    .barItem {
      width: 48px;
      height: 48px;
      border-radius: 50%;
      background-color: rgba(22, 24, 35, 0.06);
      padding: 12px;
      margin-top: 8px;
      transition: 0.3s ease all;
      cursor: pointer;
      &:hover {
        background: rgba(22, 24, 35, 0.2);
      }
      .icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        font-size: 2rem;
        color: #000;
      }
      .likeIcon {
        color: rgb(254, 44, 85);
      }
    }
    strong {
      font-size: 1.3rem;
      margin-top: 8px;
      display: block;
      color: #161823bf;
    }
  }

  .ReactModalPortal > div {
    opacity: 0;
  }

  .ReactModalPortal .ReactModal__Overlay {
    align-items: center;
    display: flex;
    justify-content: center;
    transition: opacity 200ms ease-in-out;
  }

  .ReactModal__Overlay {
    opacity: 0;
    transition: opacity 500ms ease-in-out;
  }

  .ReactModal__Overlay--after-open {
    opacity: 1;
  }

  .ReactModal__Overlay--before-close {
    opacity: 0;
  }
  .ReactModal__Body--open,
  .ReactModal__Html--open {
    overflow: hidden;
  }

  .modal-overlay {
    position: fixed;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    z-index: 5;
    background-color: rgba(0, 0, 0, 0.3);
    overflow: scroll;
  }
  .modal {
    display: flex;
    flex-direction: column;
    flex-basis: auto;
    background-color: #fff;
    max-width: 375px;
    margin: 0px auto;
  }
}

@media screen and (max-width: 500px) {
  .postItem {
    padding: 0 12px;
  }
  .postItem:not(:first-child) {
    padding: 20px 12px;
  }
  .postItem .postContent .infoContent {
    flex-direction: column;
  }
  .postItem .iconAction .barItem {
    width: 40px;
    height: 40px;
  }
  .postItem .postContent .videoItem .cardWrapper .cardImage .vlBtn .vlIcon {
    font-size: 2.5rem;
  }
  .postItem .postContent .videoItem .cardWrapper .cardImage .playBtn {
    font-size: 1.8rem;
  }
}
